@import '~react-toastify/dist/ReactToastify.min.css';

.Toastify__toast-container {
    padding: 4px;
    top: 2em;
    z-index: 10000001;
    width: fit-content !important;
    min-width: 300px;
    .Toastify__toast {
        background-color: transparent;
        min-height: none;
        padding: none;
        box-shadow: none;
        .Toastify__close-button {
            display: none;
        }
    }
}
