@import './config.variables';

body {
    margin: 0 auto;
    font-family: 'Segoe UI', 'Segoe UI Web (West European)', 'Segoe UI', -apple-system, BlinkMacSystemFont, Roboto, 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    font-size: 14px;
    font-weight: 400;
}
:focus {
    outline: none;
}
.font-weight-500 {
    font-weight: 500;
}
.App {
    min-height: 100vh;
    background-color: white;
    display: flex;
    flex-direction: column;
    .header {
        height: 42px;
        .app-title {
            float: left;
            font-size: 21px;
            margin-left: 5px;
            margin-top: 6px;
            font-weight: bold;
            color: #0078d4;
        }
        .menu-action {
            float: left;
            color: #0078d4;
            margin-top: 5px;
            margin-left: 5px;
            &:hover {
                opacity: 0.7;
            }
        }
        .account-action {
            width: fit-content;
            float: right;
            padding: 5px;
            a {
                cursor: pointer;
            }
        }
    }
    .body {
        display: flex;
        flex-direction: row;
        .content {
            background-color: #faf9f8;
            width: 100%;
            height: calc(100vh - 45px);
            box-shadow: 0 1.6px 3.6px 0 rgba(0, 0, 0, 0.132), 0 0.3px 0.9px 0 rgba(0, 0, 0, 0.108);
            .breadcrumb {
                height: 40px;
            }
            .content-data {
                padding-bottom: 0;
                height: calc(100vh - 85px);
                width: calc(100% - 10px);
                position: relative;
                .hm-command-bar {
                    border-bottom: 1px solid rgb(234, 234, 234);
                }
                .hm-filter-bar {
                    border-bottom: 1px solid rgb(234, 234, 234);
                    float: left;
                    width: 100%;
                    background-color: initial;
                    .filter-remove-all {
                        float: left;
                        width: 40px;
                        margin-top: 3px;
                    }
                    .filter-list {
                        width: calc(100% - 55px);
                        margin: 3px;
                        float: right;
                        list-style: none;
                        display: inline;
                        padding: 3px;
                        .filter-list-item {
                            float: left;
                            border: 1px solid #dddddd;
                            padding: 2px 10px;
                            padding-right: 0;
                            margin: 3px;
                            button {
                                height: 20px;
                                float: right;
                                border-left: 1px solid #ccc;
                                margin-left: 5px;
                                border-radius: 0;
                            }
                        }
                    }
                }
            }
        }

        .sidebar {
            order: -1;
            max-width: 15em;
            flex-grow: 0;
            flex-shrink: 0;
            width: 255px;
            min-width: 255px;
            flex-basis: 15em;
            padding-left: 0px;
            padding-right: 0px;
            .menu-search {
                height: 40px;
                background-color: #faf9f8;
                border-bottom: none;
            }
        }
    }
}

.hm-pagination {
    list-style: none;
    display: flex;
    padding-left: 0;
    height: 20px;
    margin: 0;
    padding-top: 3px;
    position: absolute;
    bottom: 0px;
    height: 29px;
    width: 100%;
    background-color: #faf9f8;

    .page-item {
        height: 20px;
        min-width: 20px;
        border-width: 1px;
        border-style: solid;
        border-color: rgb(50, 49, 48);
        border-image: initial;
        border-radius: 2px;
        text-align: center;
        background-color: white;
        color: rgb(50, 49, 48);
        padding: 2px;
        font-size: 15px;
        margin-left: 5px;
        transition: all 100ms ease-out;
        cursor: pointer;
        i {
            padding-top: 3px;
        }
    }
    .page-item.active {
        background-color: rgb(0, 120, 212);
        color: white;
        border: 1px solid rgb(0, 120, 212);
    }
}

.ms-Modal {
    align-items: flex-start;
    padding-top: 50px;
}
.hm-modal {
    .ms-Dialog-main {
        width: 700px;
        overflow: initial;
    }
    .modal-footer {
        padding: 20px 0 14px 0;
    }

    .ms-Modal-scrollableContent {
        overflow: initial;
        .ms-Dialog-lgHeader {
            overflow: initial;
        }
    }
}

.hm-modal.modal-lg {
    .ms-Dialog-main {
        width: 800px;
        overflow: initial;
    }
}
.ms-hm-date {
    label {
        -webkit-font-smoothing: antialiased;
        font-size: 14px;
        font-weight: 600;
        color: rgb(50, 49, 48);
        box-sizing: border-box;
        box-shadow: none;
        margin-top: 0px;
        margin-right: 0px;
        margin-bottom: 0px;
        margin-left: 0px;
        display: block;
        padding-top: 5px;
        padding-right: 0px;
        padding-bottom: 5px;
        padding-left: 0px;
        overflow-wrap: break-word;
    }
    label.required::after {
        content: ' *';
        color: rgb(164, 38, 44);
        padding-right: 12px;
    }
    .errorMessage {
        font-size: 12px;
        font-weight: 400;
        color: rgb(164, 38, 44);
        margin-top: 0px;
        margin-right: 0px;
        margin-bottom: 0px;
        margin-left: 0px;
        padding-top: 5px;
        display: flex;
        align-items: center;
    }
}

.ms-hm-select {
    label {
        -webkit-font-smoothing: antialiased;
        font-size: 14px;
        font-weight: 600;
        color: rgb(50, 49, 48);
        box-sizing: border-box;
        box-shadow: none;
        margin-top: 0px;
        margin-right: 0px;
        margin-bottom: 0px;
        margin-left: 0px;
        display: block;
        padding-top: 5px;
        padding-right: 0px;
        padding-bottom: 5px;
        padding-left: 0px;
        overflow-wrap: break-word;
    }
    label.required::after {
        content: ' *';
        color: rgb(164, 38, 44);
        padding-right: 12px;
    }
    .errorMessage {
        font-size: 12px;
        font-weight: 400;
        color: rgb(164, 38, 44);
        margin-top: 0px;
        margin-right: 0px;
        margin-bottom: 0px;
        margin-left: 0px;
        padding-top: 5px;
        display: flex;
        align-items: center;
    }
    .hm-react-select__control {
        box-shadow: none;
        margin: 0;
        padding: 1px;
        height: fit-content;
        min-height: 34px;
        padding-left: 8px;
        box-sizing: border-box;
        cursor: text;
        display: flex;
        flex-direction: row;
        align-items: stretch;
        position: relative;
        border: 1px solid $app-input-border-color;
        border-image: initial;
        border-radius: 2px;
        background: rgb(255, 255, 255);
        transition: $app-input-transition;
    }

    .hm-react-select__control:hover {
        border-color: $app-input-border-color-hover;
    }
    .hm-react-select__control--is-focused {
        border-color: rgb(0, 120, 212) !important;
        border-width: 2px;
        padding: 0;
        padding-left: 7px;
    }
    .hm-react-select__value-container,
    .react-select__value-container,
    .hm-react-select__value-container--has-value {
        padding: 0 !important;
        height: fit-content;
        font-weight: 400;
        box-shadow: none;
        margin: 0;
        padding: 0px 8px;
        box-sizing: border-box;
        color: rgb(50, 49, 48);
        width: 100%;
        min-width: 0px;
        div[class^='css-<*>-Input'] {
            margin: 1px 2px;
        }
    }
    .hm-react-select__value-container > div[class^='css-'] {
        margin: 1px 2px;
        padding: 2px;
    }
    .hm-react-select__multi-value {
        margin-bottom: 6px;
    }
    .hm-react-select__single-value,
    .hm-react-select__placeholder {
        margin-left: 5px;
        top: 14px;
        color: inherit;
    }

    .hm-react-select__indicator-separator {
        display: none;
    }
    .hm-react-select__indicator {
        padding-top: 5px;
        height: 30px;
        padding-left: 2px;
        padding-right: 2px;
        cursor: pointer;
    }
    .hm-react-select__indicators {
        min-height: 30px;
        height: fit-content;
        .hm-react-select__loading-indicator {
            padding-top: 12px;
        }
    }
    .hm-react-select__menu {
        border-radius: 0;
        z-index: 999999999;
        box-shadow: rgba(0, 0, 0, 0.133) 0px 3.2px 7.2px 0px, rgba(0, 0, 0, 0.11) 0px 0.6px 1.8px 0px;
        border-radius: 2px;
        margin-top: 2px;
    }
    .hm-react-select__menu-list {
        padding-bottom: 0px;
        padding-top: 0px;
    }
    .hm-react-select__option {
        border-radius: 0;
        cursor: pointer;
    }
    .hm-react-select__input {
        padding-left: 3px;
        input {
            margin-bottom: 5px;
        }
    }
}
.ms-hm-select.error {
    .hm-react-select__control {
        border: 1px solid rgb(164, 38, 44);
    }
    .hm-react-select__control--is-focused {
        border-color: rgb(164, 38, 44) !important;
        border-width: 2px;
    }
}
.ms-TextField:not(.has-error) {
    .ms-TextField-fieldGroup {
        border-color: $app-input-border-color;
        transition: $app-input-transition;
        height: $app-input-height;
    }
    .ms-TextField-fieldGroup:hover {
        border-color: $app-input-border-color-hover;
        transition: $app-input-transition;
    }
}

.ms-ComboBox {
    height: $app-input-height;
}
.ms-ComboBox-container {
    &.has-error {
        .ms-ComboBox::after {
            border-color: rgb(164, 38, 44) !important;
        }
    }
    &:not(.has-error) {
        .ms-ComboBox:not(.is-open)::after {
            border-color: $app-input-border-color;
            transition: $app-input-transition;
            border-width: 1px;
        }

        .ms-ComboBox:not(.is-open):hover::after {
            border-color: $app-input-border-color-hover;
            transition: $app-input-transition;
        }
    }
}

.split-btn-class {
    width: 30px;
    border: none;
    cursor: pointer;
    height: 42px;
}

.app-loading {
    position: absolute;
    min-height: 100%;
    width: 100%;
    z-index: 3;
    display: flex;
    justify-content: center;
    align-items: center;

    .background {
        position: absolute;
        min-height: 100%;
        width: 100%;
        z-index: 3;
        background-color: white;
        opacity: 0.6;
    }
}
