$darkmode-background-color: rgb(31, 31, 31);
$darkmode-text-color: rgb(208, 208, 208);
$darkmode-icon-color: rgb(75, 160, 225);
body.dark-mode {
    .App {
        background-color: $darkmode-background-color;
        color: $darkmode-text-color;
        .header {
            border-bottom: 1px solid #3f3f3f;
            .app-title {
                color: $darkmode-text-color;
            }
            .menu-action {
                color: $darkmode-text-color;
            }
        }
        .body {
            .sidebar {
                background-color: $darkmode-background-color;
                .menu-search {
                    background-color: #323130;
                }
            }
            .content {
                border-left: 1px solid #3f3f3f;
                background-color: $darkmode-background-color;
                .breadcrumb {
                    background-color: #323130;
                }
                .hm-command-bar {
                    border-bottom: 1px solid rgb(63, 63, 63);
                }
                .hm-filter-bar {
                    border-bottom: 1px solid rgb(63, 63, 63);
                }
            }
        }
    }
    .hm-pagination {
        background-color: $darkmode-background-color;
        color: $darkmode-text-color;
        .page-item {
            background-color: $darkmode-background-color;
            color: $darkmode-text-color;
        }
    }
    .split-btn-class {
        background-color: $darkmode-background-color;
        i {
            color: $darkmode-icon-color;
        }
    }
    .ms-hm-date{
        label {
            color: white;
        }
    }
    .ms-hm-select {
        label {
            color: white;
        }
        .hm-react-select__control {
            color: white;
            background-color: $darkmode-background-color;
            .hm-react-select__value-container {
                color: white;
            }
        }
        .hm-react-select__option {
            color: white;
            background-color: $darkmode-background-color;
            &.hm-react-select__option--is-focused {
                background-color: rgb(49, 49, 49);
            }
        }
        .hm-react-select__multi-value {
            color: white;
            background-color: $darkmode-background-color;
            border: 1px solid rgb(49, 49, 49);
            .hm-react-select__multi-value__label {
                color: white;
            }
            .hm-react-select__multi-value__remove:hover {
                background-color: rgb(49, 49, 49);
                color: white;
                cursor: pointer;
            }
        }
        .hm-react-select__menu-notice {
            color: white;
            background-color: $darkmode-background-color;
            border: 1px solid rgb(49, 49, 49);
        }
    }

    .ms-Overlay.ms-Overlay--dark {
        background-color: #3f3f3f;
        opacity: 0.9;
    }
    .app-loading {
        .background {
            background-color: darkgray;
        }
    }
}
